import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, IFieldOptions, FixedDataSource, MetaDataType, Case, Casings } from 'shared-code-lib';

@Component({
  selector: 'app-case',
  templateUrl: './case.component.html',
  styleUrls: ['./case.component.scss'],
})
export class CaseComponent implements OnInit {
  @Input() action: Case;

  caseField: FieldDefinition;

  get actionCasing(): string {
    return `${this.action.casing}`;
  }
  set actionCasing(val: string) {
    this.action.casing = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    const caseOptions: IFieldOptions[] = [
      {
        name: this.translate.instant('admin-metadata-formatters.lower'),
        value: `${Casings.Lower}`,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.upper'),
        value: `${Casings.Upper}`,
      },
    ];

    const caseDataSource = new FixedDataSource(new Array<IFieldOptions>());
    caseDataSource.updateOptions(caseOptions);

    this.caseField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: true,
      datasource: caseDataSource,
    });
  }
}
