export interface IFileBoundProject {
  name: string;
  id: string;
  enableSeparators: boolean;
}

export class FileBoundProject implements IFileBoundProject {
  constructor(public name: string, public id: string, public enableSeparators: boolean) {}
}

export interface IFileBoundRelatedProject {
  relatedProjectName: string;
  relatedProjectId: string;
}

export class FileBoundRelatedProject implements IFileBoundRelatedProject {
  relatedProjectName: string;
  relatedProjectId: string;

  constructor(initializer: IFileBoundRelatedProject) {
    this.relatedProjectId = initializer.relatedProjectId;
    this.relatedProjectName = initializer.relatedProjectName;
  }
}

export interface IFileBoundProjectField {
  name: string;
  number: string;
}

export class FileBoundProjectField implements IFileBoundProjectField {
  constructor(public name: string, public number: string) {}
}

export interface IFileBoundProjectFieldResult {
  project: IFileBoundProject;
  metaDataFields: Array<IFileBoundProjectField>;
  lineItemFields: Array<IFileBoundProjectField>;
  relatedProject: IFileBoundRelatedProject;
}

export class FileBoundProjectFieldResult implements IFileBoundProjectFieldResult {
  project: IFileBoundProject;
  relatedProject: IFileBoundRelatedProject;
  metaDataFields: Array<IFileBoundProjectField>;
  lineItemFields: Array<IFileBoundProjectField>;

  constructor(initializer: IFileBoundProjectFieldResult) {
    this.project = initializer.project;
    this.relatedProject = initializer.relatedProject;

    if (initializer.metaDataFields) {
      this.metaDataFields = initializer.metaDataFields.map((p) => new FileBoundProjectField(p.name, p.number));
    }

    if (initializer.lineItemFields) {
      this.lineItemFields = initializer.lineItemFields.map((p) => new FileBoundProjectField(p.name, p.number));
    }
  }
}

export interface IFileBoundProjectFile {
  name: string;
  id: string;
}

export class FileBoundProjectFile implements IFileBoundProjectFile {
  constructor(public name: string, public id: string) {}
}

export enum FileBoundFields {
  FileBoundDivider = 'FileBoundDivider',
  FileBoundSeparator = 'FileBoundSeparator',
  FileBoundDocumentName = 'FileBoundDocumentName',
}

export interface IFileBoundProjectRoute {
  name: string;
  id: string;
}

export class FileBoundProjectRoute implements IFileBoundProjectRoute {
  constructor(public name: string, public id: string) {}
}
