import { Component, Input, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  Action,
  Actions,
  Case,
  Casings,
  Criteria,
  Criterias,
  FieldDefinition,
  FixedDataSource,
  Formatter,
  IFieldOptions,
  Insert,
  Length,
  MetaDataType,
  Operators,
  Prefix,
  Remove,
  Replace,
  Suffix,
  TextCriteria,
} from 'shared-code-lib';

@Component({
  selector: 'app-formatter',
  templateUrl: './formatter.component.html',
  styleUrls: ['./formatter.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FormatterComponent implements OnInit {
  @Input() formatter: Formatter;
  @Input() index: number;
  @Output() onRemoveFormatter = new EventEmitter<number>();

  nameField: FieldDefinition;
  criteriaField: FieldDefinition;
  actionField: FieldDefinition;

  get textCriteria(): TextCriteria {
    return this.formatter.criteria as TextCriteria;
  }
  set textCriteria(c: TextCriteria) {
    this.formatter.criteria = c;
  }
  get lengthCriteria(): Length {
    return this.formatter.criteria as Length;
  }
  set lengthCriteria(c: Length) {
    this.formatter.criteria = c;
  }
  get caseAction(): Case {
    return this.formatter.action as Case;
  }
  set caseAction(val: Case) {
    this.formatter.action = val;
  }
  get prefixSuffixAction(): Prefix | Suffix {
    return this.formatter.action.actionType === 'Upland.Hydra.Core.Formatters.Prefix'
      ? (this.formatter.action as Prefix)
      : (this.formatter.action as Suffix);
  }
  set prefixSuffixAction(val: Prefix | Suffix) {
    this.formatter.action = val;
  }
  get insertAction(): Insert {
    return this.formatter.action as Insert;
  }
  set insertAction(val: Insert) {
    this.formatter.action = val;
  }
  get removeAction(): Remove {
    return this.formatter.action as Remove;
  }
  set removeAction(val: Remove) {
    this.formatter.action = val;
  }
  get replaceAction(): Replace {
    return this.formatter.action as Replace;
  }
  set replaceAction(val: Replace) {
    this.formatter.action = val;
  }

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.nameField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'formatter-name',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.formatter-name-placeholder'),
      required: true,
    });

    const criteriaOptions = [
      {
        name: this.translate.instant('admin-metadata-formatters.starts-with'),
        value: Criterias.StartsWith,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.ends-with'),
        value: Criterias.EndsWith,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.contains'),
        value: Criterias.Contains,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.matches-regex'),
        value: Criterias.Regex,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.length'),
        value: Criterias.Length,
      },
    ];

    const actionOptions = [
      {
        name: this.translate.instant('admin-metadata-formatters.change-case'),
        value: Actions.Case,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.prefix-with'),
        value: Actions.Prefix,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.suffix-with'),
        value: Actions.Suffix,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.insert'),
        value: Actions.Insert,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.remove'),
        value: Actions.Remove,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.replace'),
        value: Actions.Replace,
      },
    ];

    const criteriaDataSource = new FixedDataSource(new Array<IFieldOptions>());
    const actionDataSource = new FixedDataSource(new Array<IFieldOptions>());

    criteriaDataSource.updateOptions(criteriaOptions);
    actionDataSource.updateOptions(actionOptions);

    this.criteriaField = new FieldDefinition({
      type: MetaDataType.List,
      label: 'text-formatting-criteria',
      hideLabel: true,
      placeholder: '',
      required: true,
      datasource: criteriaDataSource,
    });

    this.actionField = new FieldDefinition({
      type: MetaDataType.List,
      label: 'text-formatting-action',
      hideLabel: true,
      placeholder: '',
      required: true,
      datasource: actionDataSource,
    });
  }

  onCriteriaChanged(criteriaType: string) {
    if (criteriaType === Criterias.Length) {
      this.formatter.criteria = new Length({
        criteriaType: criteriaType,
        operand: 0,
        operator: Operators.Equals,
      });
    } else {
      this.formatter.criteria = Criteria.create({
        criteriaType: criteriaType,
        text: '',
        ignoreCase: false,
      });
    }
  }

  onActionChanged(actionType: string) {
    if (actionType == Actions.Case) {
      this.formatter.action = new Case({
        actionType: actionType,
        casing: Casings.Lower,
      });
    } else if (actionType == Actions.Prefix || actionType == Actions.Suffix) {
      this.formatter.action = Action.create({
        actionType: actionType,
        text: '',
      });
    } else if (actionType == Actions.Replace) {
      this.formatter.action = new Replace({
        actionType: actionType,
        target: '',
        replaceWith: '',
        ignoreCase: false,
      });
    } else if (actionType == Actions.Remove) {
      this.formatter.action = new Remove({
        actionType: actionType,
        count: 0,
        position: 0,
        untilLengthIs: 0,
      });
    } else if (actionType == Actions.Insert) {
      this.formatter.action = new Insert({
        actionType: actionType,
        text: '',
        position: 0,
        untilLengthIs: 0,
      });
    }
  }

  deleteFormatter() {
    this.onRemoveFormatter.emit(this.index);
  }
}
