import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, Replace } from 'shared-code-lib';

@Component({
  selector: 'app-replace',
  templateUrl: './replace.component.html',
  styleUrls: ['./replace.component.scss'],
})
export class ReplaceComponent implements OnInit {
  @Input() action: Replace;
  @Input() id: string = '';

  targetField: FieldDefinition;
  replaceWithField: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.targetField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'target',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.target-placeholder'),
      required: true,
    });

    this.replaceWithField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'replace-with',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.replace-with-placeholder'),
      required: true,
    });
  }
}
