import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { Log } from '../core/log';
import {
  FileBoundProject,
  FileBoundProjectFieldResult,
  FileBoundProjectFile,
  FileBoundProjectRoute,
  IFileBoundProject,
  IFileBoundProjectFieldResult,
  IFileBoundProjectRoute,
} from '../models/filebound';

class NetDocsRefreshTokenResponse {
  refreshToken: string;
}

@Injectable({
  providedIn: 'root',
})
export class ProxyApiService {
  constructor(private http: HttpClient) {}

  public getFileBoundProjects(
    fileboundurl: string,
    username: string,
    password: string,
    successCallBack: (projects: Array<FileBoundProject>) => void,
    failureCallBack: (err: any) => void
  ): void {
    const url = `api/proxy/fileboundprojects?fileboundurl=${encodeURIComponent(
      fileboundurl
    )}&username=${username}&password=${encodeURIComponent(password)}`;

    this.http.get(url).subscribe((projects: Array<IFileBoundProject>) => {
      successCallBack(projects.map((p) => new FileBoundProject(p.name, p.id, p.enableSeparators)));
    }, failureCallBack);
  }

  public getFileBoundProjectFields(
    fileboundurl: string,
    username: string,
    password: string,
    projectid: string,
    successCallBack: (result: FileBoundProjectFieldResult) => void,
    failureCallBack: (err: any) => void
  ): void {
    const url = `api/proxy/fileboundprojectfields?fileboundurl=${encodeURIComponent(
      fileboundurl
    )}&username=${username}&password=${encodeURIComponent(password)}&projectid=${projectid}`;

    this.http.get(url).subscribe((result: IFileBoundProjectFieldResult) => {
      successCallBack(new FileBoundProjectFieldResult(result));
    }, failureCallBack);
  }

  public getFileBoundProjectFiles(
    fileboundurl: string,
    username: string,
    password: string,
    projectId: string,
    successCallBack: (files: Array<FileBoundProjectFile>) => void,
    failureCallBack: (err: any) => void
  ): void {
    const url = `api/proxy/fileboundprojectfiles?fileboundurl=${encodeURIComponent(
      fileboundurl
    )}&username=${username}&password=${encodeURIComponent(password)}&projectid=${projectId}`;

    this.http.get(url).subscribe((files: Array<FileBoundProjectFile>) => {
      successCallBack(files.map((p) => new FileBoundProjectFile(p.name, p.id)));
    }, failureCallBack);
  }

  public getFileBoundProjectRoutes(
    fileboundurl: string,
    username: string,
    password: string,
    projectId: string,
    successCallBack: (routes: Array<FileBoundProjectRoute>) => void,
    failureCallBack: (err: any) => void
  ): void {
    const url = `api/proxy/fileboundprojectroutes?fileboundurl=${encodeURIComponent(
      fileboundurl
    )}&username=${username}&password=${encodeURIComponent(password)}&projectid=${projectId}`;

    this.http.get(url).subscribe((routes: Array<IFileBoundProjectRoute>) => {
      successCallBack(routes.map((p) => new FileBoundProjectRoute(p.name, p.id)));
    }, failureCallBack);
  }

  public checkInterFAXConnectivity(
    username: string,
    password: string,
    successCallBack: () => void,
    failureCallBack: (err: any) => void
  ): void {
    const url = `api/proxy/checkInterFAXConnectivity?username=${username}&password=${encodeURIComponent(password)}`;
    this.http.get(url).subscribe(successCallBack, failureCallBack);
  }

  public netDocsExchangeCodeForRefreshToken(
    vault: string,
    code: string,
    successCallBack: (token: string) => void,
    failureCallBack: (err: any) => void
  ): void {
    Log.enter('ProxyApiService.netDocsExchangeCodeForRefreshToken', vault, code);

    const url = `api/proxy/netDocsExchangeCodeForRefreshToken?vault=${vault}&code=${encodeURIComponent(code)}`;
    this.http.get(url).subscribe((r: NetDocsRefreshTokenResponse) => successCallBack(r.refreshToken), failureCallBack);
  }
}
