import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, Insert, MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-insert',
  templateUrl: './insert.component.html',
  styleUrls: ['./insert.component.scss'],
})
export class InsertComponent implements OnInit {
  @Input() action: Insert;

  textField: FieldDefinition;
  positionField: FieldDefinition;
  untilLengthIsField: FieldDefinition;

  get actionPosition(): string {
    return `${this.action.position}`;
  }
  set actionPosition(val: string) {
    this.action.position = Number(val);
  }
  get actionUntilLengthIs(): string {
    return `${this.action.untilLengthIs}`;
  }
  set actionUntilLengthIs(val: string) {
    this.action.untilLengthIs = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.textField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'insert',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.text-placeholder'),
      required: true,
    });

    this.positionField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: 'insert-position',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.position-placeholder'),
      required: true,
    });

    this.untilLengthIsField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: 'insert-until',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.until-length-placeholder'),
      required: true,
    });
  }
}
