import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, IFieldOptions, FixedDataSource, MetaDataType, Length, Operators } from 'shared-code-lib';

@Component({
  selector: 'app-length',
  templateUrl: './length.component.html',
  styleUrls: ['./length.component.scss'],
})
export class LengthComponent implements OnInit {
  @Input() criteria: Length;

  operandField: FieldDefinition;
  operatorField: FieldDefinition;

  get criteriaOperator(): string {
    return `${this.criteria.operator}`;
  }
  set criteriaOperator(val: string) {
    this.criteria.operator = Number(val);
  }
  get criteriaOperand(): string {
    return `${this.criteria.operand}`;
  }
  set criteriaOperand(val: string) {
    this.criteria.operand = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.operandField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: 'length-criteria',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.operand-placeholder'),
      required: true,
    });

    const operatorOptions: IFieldOptions[] = [
      {
        name: this.translate.instant('admin-metadata-formatters.equals'),
        value: `${Operators.Equals}`,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.not-equals'),
        value: `${Operators.NotEquals}`,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.greater-than'),
        value: `${Operators.GreaterThan}`,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.less-than'),
        value: `${Operators.LessThan}`,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.greater-than-or-equals'),
        value: `${Operators.GreaterThanOrEquals}`,
      },
      {
        name: this.translate.instant('admin-metadata-formatters.less-than-or-equals'),
        value: `${Operators.LessThanOrEquals}`,
      },
    ];

    const operatorDataSource = new FixedDataSource(new Array<IFieldOptions>());
    operatorDataSource.updateOptions(operatorOptions);

    this.operatorField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: true,
      datasource: operatorDataSource,
    });
  }
}
