import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, Remove } from 'shared-code-lib';

@Component({
  selector: 'app-remove',
  templateUrl: './remove.component.html',
  styleUrls: ['./remove.component.scss'],
})
export class RemoveComponent implements OnInit {
  @Input() action: Remove;

  countField: FieldDefinition;
  positionField: FieldDefinition;
  untilLengthIsField: FieldDefinition;

  get actionCount(): string {
    return `${this.action.count}`;
  }
  set actionCount(val: string) {
    this.action.count = Number(val);
  }
  get actionPosition(): string {
    return `${this.action.position}`;
  }
  set actionPosition(val: string) {
    this.action.position = Number(val);
  }
  get actionUntilLengthIs(): string {
    return `${this.action.untilLengthIs}`;
  }
  set actionUntilLengthIs(val: string) {
    this.action.untilLengthIs = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.countField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: 'removal-count',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.count-placeholder'),
      required: true,
    });

    this.positionField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: 'removal-position',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.position-placeholder'),
      required: true,
    });

    this.untilLengthIsField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: 'removal-until-length',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.until-length-placeholder'),
      required: true,
    });
  }
}
