import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, TextCriteria } from 'shared-code-lib';

@Component({
  selector: 'app-text-criteria',
  templateUrl: './text.component.html',
  styleUrls: ['./text.component.scss'],
})
export class TextCriteriaComponent implements OnInit {
  @Input() criteria: TextCriteria;
  @Input() id: string = '';
  textField: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.textField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'formatter-text',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.text-placeholder'),
      required: true,
    });
  }
}
