import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, Prefix, Suffix } from 'shared-code-lib';

@Component({
  selector: 'app-prefix-and-suffix',
  templateUrl: './prefix-and-suffix.component.html',
  styleUrls: ['./prefix-and-suffix.component.scss'],
})
export class PrefixAndSuffixComponent implements OnInit {
  @Input() action: Prefix | Suffix;
  textField: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.textField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'formatter-prefix-and-suffix',
      hideLabel: true,
      placeholder: this.translate.instant('admin-metadata-formatters.text-placeholder'),
      required: true,
    });
  }
}
