import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import {
  Action,
  Actions,
  Casings,
  Criteria,
  Criterias,
  DelayService,
  FieldDefinition,
  Formatter,
  MetaDataType,
} from 'shared-code-lib';
import { FormattingResults, TestFormattingParameters } from '../models/metadata-formatting';
import { HydraApiService } from '../services/hydra-api.service';
import { SpinnerStateService } from '../services/spinner-state.service';

@Component({
  selector: 'app-formatters',
  templateUrl: './formatters.component.html',
  styleUrls: ['./formatters.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FormattersComponent implements OnInit, AfterViewInit {
  @ViewChild(PerfectScrollbarDirective, { static: false }) directiveRef?: PerfectScrollbarDirective;
  @Input() formatters: Array<Formatter>;
  @Input() testHeaderText: string;

  testField: FieldDefinition;
  testFormattersModal: NgxSmartModalComponent;
  valueToBeTested: string = '';
  formattingResults: FormattingResults;
  errors: string[] = [];
  @ViewChild('results', { static: false }) resultsDiv: ElementRef;

  constructor(
    private ngxSmartModalService: NgxSmartModalService,
    private translate: TranslateService,
    private hydraApiService: HydraApiService,
    private spinnerStateService: SpinnerStateService,
    private toastr: ToastrService,
    private delay: DelayService
  ) {}

  ngOnInit(): void {
    this.createFields();
    this.errors = [];
  }

  ngAfterViewInit() {
    this.testFormattersModal = this.ngxSmartModalService.get('testFormattersModal');
  }

  createFields() {
    this.testField = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: this.translate.instant('admin-metadata-formatters.enter-value'),
      required: true,
    });
  }

  addNewFormatter = (): void => {
    this.formatters.push(
      new Formatter({
        name: '',
        action: Action.create({
          actionType: Actions.Case,
          casing: Casings.Upper,
        }),
        criteria: Criteria.create({
          criteriaType: Criterias.StartsWith,
          text: '',
          ignoreCase: false,
        }),
        doNotApplyFurtherFormatting: false,
      })
    );

    this.delay.execute(this.onAddingNewFormatter, 10);
  };

  onAddingNewFormatter = (): void => {
    this.directiveRef.scrollToBottom(0, 100);
    const input = document.querySelector(`#formatter-${this.formatters.length - 1} input:first-of-type`);
    if (input) (input as HTMLElement).focus();
  };

  removeFormatter(index: number) {
    this.formatters.splice(index, 1);
    this.directiveRef.update();
  }

  moveItem(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.formatters, event.previousIndex, event.currentIndex);
  }

  openTestFormattersModal() {
    this.testFormattersModal.open();
    setTimeout(this.focusValueInput, 50);
  }

  focusValueInput = () => {
    const valueToBeTestedInput = <HTMLInputElement>document.querySelector('.test-field-container input');
    valueToBeTestedInput.focus();
  };

  cancelTestFormatters() {
    this.errors = [];
    this.testFormattersModal.close();
    this.valueToBeTested = '';
    this.formattingResults = undefined;
  }

  testFormatters() {
    this.errors = [];
    if (this.valueToBeTested == '') {
      this.errors.push(this.translate.instant('admin-metadata-formatters.empty-value'));
      return;
    }

    const params = new TestFormattingParameters();
    params.value = this.valueToBeTested;
    params.formatters = this.formatters;

    const loadingText = this.translate.instant('admin-metadata-formatters.applying-formatting');

    this.spinnerStateService.show(loadingText, this.resultsDiv.nativeElement);
    this.hydraApiService.testMetaDataFormatting(params, this.testFormattersSuccess, this.testFormattersFails);
  }

  testFormattersSuccess = (result: FormattingResults) => {
    this.spinnerStateService.hide();
    this.formattingResults = result;
  };

  testFormattersFails = (err: any) => {
    this.spinnerStateService.hide();
    this.toastr.error(err.message);
  };

  onInputChange(ev) {
    this.formattingResults = undefined;
  }
}
